@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "BebasNeue";

  font-display: swap;
  src: local("BebasNeue"),
    url("../fonts/BebasNeue-Regular.woff2") format("woff2"),
    url("../fonts/BebasNeue-Regular.woff");
  unicode-range: U+000-5FF;
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "BebasNeue";

  font-display: swap;
  src: local("BebasNeue"), url("../fonts/BebasNeue-Bold.woff2") format("woff2"),
    url("../fonts/BebasNeue-Bold.woff");
  unicode-range: U+000-5FF;
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "RobotoCondensed";

  font-display: swap;
  src: local("RobotoCondensed"),
    url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"),
    url("../fonts/RobotoCondensed-Bold.woff");
  unicode-range: U+000-5FF;
}

@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "RobotoCondensed";

  font-display: swap;
  src: local("RobotoCondensed"),
    url("../fonts/RobotoCondensed-Light.woff2") format("woff2"),
    url("../fonts/RobotoCondensed-Light.woff");
  unicode-range: U+000-5FF;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "RobotoCondensed";

  font-display: swap;
  src: local("Roboto-Condensed"),
    url("../fonts/Roboto-Condensed.woff2") format("woff2"),
    url("../fonts/Roboto-Condensed.woff");
  unicode-range: U+000-5FF;
}
